.header__bar span {
  display: block;
  position: absolute;
  top: 26px;
  left: 0;
  right: 0;
  height: 4px;
  background: #111546;
  transition: background 0.3s 0.3s;
  border-radius: 6px;
}

.header__bar span::before,
.header__bar span::after {
  content: '';
  display: block;
  width: 100%;
  height: 4px;
  background-color: #111546;
  position: absolute;
  transition-duration: 0.3s, 0.3s;
  transition-delay: 0.3s, 0.3s;
  border-radius: 6px;
}

.header__bar span::before {
  transition-property: top, transform;
  top: -12px;
}

.header__bar span::after {
  transition-property: bottom, transform;
  bottom: -12px;
}

